.favorite{
    > div{
        display: flex;
        flex-direction: column;
        align-items: center;
        > h2{
            font-weight: bold;
            font-size: 24px;
            margin: 20px 0px 0px;
        }
        > p{
            font-size: 18px;
            text-align: center;
            margin: 20px 0px;
        }
        > button{
            font-size: 18px;
            padding:5px 50px;
            background-color: black;
            color: white;
        }
    }
}